.home {
	height: 100vh;
	background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
		url("../../assets/home.png");

	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	animation: slideBg 20s ease-in-out infinite 8s;
	animation-timing-function: ease-in-out;
	background-repeat: no-repeat;
	background-repeat: no-repeat;
	display: flex;
	flex-direction: column;
	gap: 7rem;
	padding: 5%;
	position: relative;
	margin: 0;
	overflow: hidden;
}

@keyframes slideBg {
	0% {
		background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
			url("../../assets/clock.jpg");
		background-size: cover;
	}

	25% {
		background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
			url("../../assets/home.png");
		background-size: cover;
	}

	50% {
		background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
			url("../../assets/clock.jpg");

		background-size: cover;
	}

	75% {
		background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
			url("../../assets/home.png");
		background-size: cover;
	}

	100% {
		background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
			url("../../assets/clock.jpg");
		background-size: cover;
	}
}

@media screen and (max-width: 550px) {
	@keyframes slideBg {
		0% {
			background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
				url("../../assets/clock.jpg");
			background-size: cover;
			background-position: center center;
		}

		25% {
			background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
				url("../../assets/home.png");
			background-size: cover;
			background-position: center center;
		}

		50% {
			background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
				url("../../assets/clock.jpg");
			background-position: center center;
			background-size: cover;
		}

		75% {
			background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
				url("../../assets/home.png");
			background-position: center center;
			background-size: cover;
		}

		100% {
			background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
				url("../../assets/clock.jpg");
			background-position: center center;
			background-size: cover;
		}
	}
}

@media screen and (min-width: 768px) {
	.home {
		gap: 4rem;
	}
}

/* nav styles */
.home .homeNav {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}
.home .homeNav .logo {
	width: 70px;
	height: 70px;
}
.home .homeNav .logo svg,
.home .homeNav .logo img {
	max-width: 100%;
	height: 100%;
}

.home .homeNav .admin a {
	border: none;
	color: #fff;
	display: flex;
	align-items: center;
	gap: 0.5rem;
	font-size: 0.75rem;
	outline: none;
	text-align: center;
	text-decoration: underline;
}

@media screen and (min-width: 768px) {
	.home .homeNav .admin a {
		font-size: 1rem;
	}
}

@media screen and (min-width: 1024px) {
	.home .homeNav {
		position: absolute;
		top: 1rem;
		width: 90%;
	}

	/* .home .homeNav .logo {
		width: 70px;
	} */
}

/* main content styles */
.home main {
	display: flex;
	flex-direction: column;
	gap: 5rem;
}

.home main .title h1 {
	font-size: 2rem;
	color: white;
}

@media screen and (min-width: 768px) {
	.home main {
		gap: 3rem;
	}

	.home main .title h1 {
		font-size: 3rem;
	}
}

@media screen and (min-width: 1024px) {
	.home main {
		margin: 5rem 0;
	}

	.home main .title h1 {
		font-size: 4rem;
	}
}

@media screen and (min-width: 1440px) {
	.home main {
		margin: auto 0;
	}
}

.home main .btnWrapper {
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.home main .btnWrapper .registerLink {
	display: flex;
	gap: 0.5rem;
	color: white;
}

.home main .btnWrapper .registerLink a {
	color: #fff;
	text-decoration: underline;
}

@media screen and (min-width: 768px) {
	.home main .btnWrapper .registerLink {
		font-size: 0.8rem;
	}
}

/* modal styles */
.modalContainer {
	display: flex;
	flex-direction: row;
	margin-bottom: 5%;
	padding: 5%;
	justify-content: space-around;
	align-items: center;
}
.face {
	height: 100px;
	display: flex;
	flex-direction: column;

	width: 100px;
	text-align: center;
	font-size: 18px;
}

.face svg {
	display: flex;

	width: 100%;
	height: 100%;
	display: flex;
}
